
import { Options, setup, Vue } from 'vue-class-component';

import { UrlSearchKeysDictionary } from '@/types/common';

import { useConsultationInfo, useOrderConsultationIfOrder } from '@/composable';

import { RCard } from '@/ui/molecules';
import {
  RImg, RBtn, RSpinner, RSkeleton,
} from '@/ui/atoms';
import { ModalLayout } from '@/ui/layout';
import { ImageWithDesc } from '@/ui/organisms';

@Options({
  components: {
    RCard,
    RImg,
    RBtn,
    RSpinner,
    ModalLayout,
    RSkeleton,
    ImageWithDesc,
  },
})
export default class InfoAfterPayment extends Vue {
  consultation = setup(useOrderConsultationIfOrder);

  consultationInfo = setup(useConsultationInfo);

  get date(): string {
    const { calculatedDateStart } = this.consultation.consultation.data ?? {};

    if (!calculatedDateStart) {
      return '';
    }

    const date = this.$dateFilters.formatDayMonth(calculatedDateStart);
    const time = this.$dateFilters.formatHoursMinutes(calculatedDateStart);

    return `${date} в ${time}`;
  }

  closeWebview(): void {
    const { interval, doctorNameWithSpeciality } = this.consultationInfo.consultationInfoStore;
    const consultationInfo = this.$urlUtils.createUrlSearchParams<UrlSearchKeysDictionary>({
      doctorNameWithSpeciality,
      beginInterval: interval?.beginInterval,
    });

    this.$router.push(`${this.$webviewPathsMessages.CLOSE_WEBVIEW}${consultationInfo}`);
  }
}
